@import url(./vendor/swiper-bundle.min.css);
:root {
  --color-black: #000;
  --color-white: #fff;
  --color-amethyst: #9d5cd0;
  --color-amethyst-transparrent: rgba(157, 92, 208, 0.5);
  --color-dull-gray: #666;
  --color-signal-black: #333;
  --color-light-gray: #999;
  --color-telegrey: #cacaca;
  --color-smoky-white: #ececec;
  --color-krayola: #7943a4;
  --color-violet: #c283f3;
}

@font-face {
  font-family: "OpenSans";
  src: url("../fonts/OpenSans-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "OpenSans";
  src: url("../fonts/OpenSans-SemiBold.woff2") format("woff2");
  font-weight: 600;
  font-display: swap;
  font-style: normal;
}

