html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "OpenSans", sans-serif;
  font-weight: 400;
  background-color: var(--color-white);
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  padding: 0;
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.site-container {
  overflow: hidden;
}

.stop-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

.is-hidden {
  display: none !important;
}

.for-mobile {
  display: none;
}

@media (max-width: 1024px) {
  .for-mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 50px;
  max-width: 1700px;
}

@media (max-width: 768px) {
  .container {
    padding: 0 15px;
  }
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.input {
  outline: none;
  border: none;
  padding: 0;
  background-color: transparent;
}

.btn {
  border: 2px solid var(--color-amethyst);
  border-radius: 100px;
  padding: 20px 33px;
  font-weight: 400;
  font-size: 18px;
  cursor: pointer;
}

.title {
  margin: 0 0 20px;
  padding: 0;
  font-weight: 400;
  font-size: 48px;
  color: var(--color-signal-black);
}

.text {
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 32px;
  color: var(--color-signal-black);
}

.descr {
  font-size: 12px;
  line-height: 16px;
  color: var(--color-light-gray);
}

.link {
  color: var(--color-amethyst);
}

.link:hover {
  text-decoration: underline;
}

.subtitle {
  margin: 0;
  font-weight: 600;
  font-size: 24px;
  line-height: 32px;
  color: var(--color-signal-black);
}

.header {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1;
  width: 100%;
}

.header__top {
  background-color: var(--color-black);
}

.header__bottom {
  background-color: rgba(0, 0, 0, 0.4);
}

.header-top__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 24px;
  padding-bottom: 19px;
}

.header-top__logo {
  min-width: 173px;
}

.nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.nav__list {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-right: 280px;
}

.nav__item:not(:last-child) {
  margin-right: 62px;
}

.nav__link {
  font-weight: 600;
  font-size: 18px;
  line-height: 25px;
  color: var(--color-white);
}

.nav__btn {
  padding-right: 0;
  padding-left: 20px;
  font-weight: 400;
  font-size: 18px;
  color: var(--color-white);
  background: url("../img/login.svg") no-repeat left center;
}

.header-bottom__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 24px;
  padding-bottom: 21px;
}

.header-bottom__select {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header-select {
  position: relative;
  user-select: none;
}

.header-select:not(:last-child) {
  margin-right: 101px;
}

.header-select__top {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-weight: 400;
  font-size: 18px;
  line-height: 25px;
  color: var(--color-white);
  cursor: pointer;
}

.header-select__top svg {
  display: block;
  margin-left: 2px;
  width: 10px;
  height: 10px;
  fill: currentColor;
  object-fit: cover;
  transform: rotate(90deg);
}

.header-select__dropdown {
  position: absolute;
  left: -10px;
  top: 10px;
  z-index: 20;
  padding: 10px;
  width: 100%;
  height: 50px;
}

.header-select__list {
  display: none;
  width: 100%;
  height: 50px;
}

.header-select__item {
  font-weight: 600;
  font-size: 14px;
  line-height: 19px;
  color: var(--color-white);
}

.header-select__item:not(:last-child) {
  margin-bottom: 10px;
}

.search label {
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: var(--color-white);
}

.search label svg {
  display: block;
  margin-right: 9px;
  width: 22px;
  height: 22px;
  fill: currentColor;
}

.search label input {
  border-bottom: 1px solid rgba(255, 255, 255, 0.85);
  padding-bottom: 4px;
  max-width: 219px;
  font-weight: 400;
  font-size: 18px;
  color: currentColor;
}

.search label input::placeholder {
  color: var(--color-white);
  opacity: 0.85;
}

.search label:focus-within {
  color: var(--color-violet);
}

.search label:hover {
  color: var(--color-violet);
}

.search label:hover input {
  border-color: currentColor;
}

.search:focus-within {
  outline: 2px solid var(--color-krayola);
}

