.hero {
  position: relative;
}

.hero__img {
  position: absolute;
  left: 0;
  top: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.hero__container {
  padding-top: 232px;
  padding-bottom: 123px;
}

.hero__title {
  margin: 0 0 0 -5px;
  padding: 5px 0 0;
  width: 100%;
  max-width: 570px;
  font-size: 72px;
  line-height: 85px;
  letter-spacing: 1.9px;
  color: var(--color-white);
}

.hero__text {
  margin-bottom: 93px;
  width: 100%;
  max-width: 580px;
  font-size: 18px;
  line-height: 30px;
  letter-spacing: 1px;
  color: var(--color-white);
}

.hero__btn {
  font-weight: 600;
  font-size: 18px;
  line-height: 25px;
  letter-spacing: 0.4px;
  color: var(--color-white);
  background-color: var(--color-amethyst);
}

.about__container {
  padding-top: 87px;
  padding-bottom: 69px;
}

.about__text {
  width: 100%;
  max-width: 1086px;
}

.gallery {
  background: url("../img/gallery/background.jpg") center no-repeat;
  background-size: cover;
}

.gallery__container {
  display: flex;
  justify-content: space-between;
  padding-top: 85px;
  padding-bottom: 53px;
}

.gallery__left {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
  width: 100%;
  max-width: 25%;
}

.gallery__right {
  position: relative;
  padding-top: 18px;
  width: 71.9%;
}

.gallery__title {
  margin-bottom: 28px;
  color: var(--color-white);
}

.gallery__descr {
  font-weight: 600;
  font-size: 16px;
  color: var(--color-white);
}

.gallery__text {
  max-width: 350px;
  font-size: 16px;
  line-height: 32px;
  color: var(--color-white);
}

.gallery-select {
  position: relative;
  width: 100%;
  user-select: none;
}

.gallery-select__top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border: 1px solid var(--color-white);
  border-radius: 6px;
  padding: 10px 15px;
  font-size: 16px;
  color: var(--color-white);
}

.gallery-select__top svg {
  display: block;
  width: 10px;
  height: 10px;
  fill: currentColor;
  transform: rotate(90deg);
}

.gallery-select__dropdown {
  position: absolute;
  left: 0;
  top: 24px;
  width: 100%;
}

.gallery-select__list {
  border: 1px solid var(--color-light-gray);
  border-radius: 6px;
}

.gallery-select__item {
  padding: 10px 15px;
  font-size: 16px;
  color: var(--color-light-gray);
}

.gallery-select__item:not(:last-child) {
  border-bottom: 1px solid var(--color-light-gray);
}

.gallery-left__top {
  margin-bottom: 540px;
  width: 100%;
}

.swiper-container {
  margin-right: auto;
  margin-left: auto;
  width: 100%;
  height: 810px;
}

.swiper-slide {
  width: 100%;
  height: 100%;
  max-width: 350px;
  max-height: 350px;
}

.swiper-slide img {
  height: auto;
  max-width: 100%;
}

.swiper-pagination-block {
  position: relative;
  margin-top: 10px;
  margin-bottom: 22px;
  width: 130px;
  height: 30px;
}

.swiper-pagination {
  top: 0;
  color: var(--color-white);
}

.swiper-button-next,
.swiper-button-prev {
  right: 0;
  z-index: 100;
  border-radius: 50%;
  padding: 10px;
  width: 30px;
  height: 30px;
  background-color: var(--color-dull-gray);
}

.swiper-button-next::after,
.swiper-button-prev::after {
  font-size: inherit;
  color: var(--color-white);
}

.swiper-button-prev {
  left: 0;
}

.catalog__container {
  padding-top: 74px;
  padding-bottom: 80px;
}

.catalog-top {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-start;
}

.catalog-top__title {
  margin-top: 11px;
  margin-right: 39px;
}

.catalog-top__text {
  margin-bottom: 40px;
  width: 100%;
  max-width: 1100px;
}

.flags__list {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.flags__item {
  position: relative;
  width: 46px;
  height: 46px;
  background-size: cover;
  background-repeat: no-repeat;
  cursor: pointer;
}

.flags__item:not(:last-child) {
  margin-right: 50px;
}

.flags__item--france {
  background-image: url("../img/catalog/france.png");
}

.flags__item--germany {
  background-image: url("../img/catalog/germany.png");
}

.flags__item--italy {
  background-image: url("../img/catalog/italy.png");
}

.flags__item--russia {
  background-image: url("../img/catalog/russia.png");
}

.flags__item--belgium {
  background-image: url("../img/catalog/belgium.png");
}

.flags__item--active::before {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  border: 2px solid var(--color-amethyst);
  border-radius: 50%;
  width: 66px;
  height: 66px;
  transform: translate(-50%, -50%);
}

.catalog-bottom {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}

.catalog-bottom__left {
  width: 47%;
}

.catalog-bottom__right {
  width: 50%;
}

.painter__name {
  margin: 28px 0 5px;
}

.painter__text {
  margin-top: 6px;
}

.accordion__title {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0;
  border-bottom: 1px solid var(--color-telegrey);
  padding-top: 30px;
  padding-bottom: 33px;
}

.accordion__title span {
  position: relative;
  border: 2px solid var(--color-smoky-white);
  border-radius: 50%;
  width: 50px;
  height: 50px;
  background-color: var(--color-smoky-white);
  transform: rotate(90deg);
  transition: 0.3s transform;
}

.accordion__title span svg {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 15px;
  height: 15px;
  fill: var(--color-black);
  transform: translate(-50%, -50%);
}

.accordion__title:first-child {
  border-top: 1px solid var(--color-telegrey);
}

.accordion__lists {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: flex-start;
}

.accordion__list {
  padding-top: 10px;
  padding-bottom: 11px;
}

.accordion__list:not(:last-child) {
  margin-right: 70px;
}

.accordion__item {
  font-size: 16px;
  line-height: 32px;
}

.accordion__link {
  color: var(--color-signal-black);
}

.accordion__link--active {
  color: var(--color-amethyst);
}

.accordion .ui-state-active {
  border-bottom-color: var(--color-amethyst);
}

.accordion .ui-state-active > span {
  transform: rotate(270deg);
  transition: 0.3s transform;
}

.accordion .ui-accordion-content-active {
  border-bottom: 1px solid var(--color-amethyst);
}

.events__container {
  padding-top: 7px;
  padding-bottom: 81px;
}

.events__list {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: space-between;
  width: 100%;
}

.events__btn {
  display: block;
  margin: 0 auto;
  padding: 17px 35px;
  font-weight: 600;
  font-size: 18px;
  color: var(--color-amethyst);
}

.event {
  margin-bottom: 34px;
  width: 31.2%;
  height: 100%;
  max-height: 700px;
}

.event:not(:nth-child(-n + 3)) {
  margin-bottom: 0;
}

.event__img {
  height: auto;
  max-width: 100%;
}

.event__about {
  margin-top: -4px;
  border: 1px solid var(--color-telegrey);
  border-top: none;
}

.event__spans {
  padding: 15px 17px 0;
}

.event__descr {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
  padding: 4px 42px 48px 50px;
  height: 353px;
}

.event__link {
  border-bottom: 1px solid currentColor;
  padding-bottom: 3px;
  font-size: 16px;
  color: var(--color-amethyst);
}

.event__place, .event__date {
  display: block;
  text-align: right;
}

.event--hidden {
  display: none;
}

.event-descr__top {
  overflow: hidden;
  margin-bottom: 20px;
}

.editions {
  background: url("../img/editions/background.jpg") no-repeat center;
  background-size: cover;
}

.editions__container {
  display: flex;
  justify-content: space-between;
  padding-top: 85px;
