.projects__text {
  margin-bottom: 21px;
  max-width: 65.7%;
}

.projects__text span {
  position: relative;
  display: inline-block;
  border: 1px solid var(--color-amethyst);
  border-radius: 50%;
  width: 16px;
  height: 16px;
  background-color: transparent;
  transition: background-color 0.3s;
}

.projects__text span::before {
  content: "";
  position: absolute;
  left: 50%;
  bottom: 55%;
  border-style: solid;
  border-width: 10px;
  border-color: var(--color-telegrey) transparent transparent transparent;
  width: 0;
  height: 0;
  opacity: 0;
  transform: translateX(-50%);
  transition: opacity 0.3s;
}

.projects__text span::after {
  content: attr(data-tooltip);
  position: absolute;
  left: 50%;
  bottom: 200%;
  padding: 10px;
  width: 264px;
  font-weight: 600;
  font-size: 12px;
  line-height: 16px;
  text-align: center;
  color: var(--color-white);
  background-color: var(--color-telegrey);
  opacity: 0;
  transform: translateX(-50%);
  transition: opacity 0.3s;
}

.projects__text span svg {
  position: absolute;
  right: 35%;
  top: 50%;
  display: block;
  width: 2px;
  height: 10px;
  text-align: center;
  fill: var(--color-amethyst);
  transform: translate(-50%, -50%);
  transition: fill 0.3s;
}

.projects__text span:hover {
  background-color: var(--color-amethyst);
  transition: background-color 0.3s;
}

.projects__text span:hover::before {
  opacity: 1;
  transition: opacity 0.3s;
}

.projects__text span:hover::after {
  opacity: 1;
  transition: opacity 0.3s;
}

.projects__text span:hover svg {
  fill: var(--color-white);
  transition: fill 0.3s;
}

